.leaflet-control-window-wrapper {
  opacity: 0;
  -webkit-overflow-scrolling: touch;
  box-sizing: border-box;
  display: none;
}

.nonmodal {
  z-index: 6000;
}

.modal {
  width: 100%;
  height: 100%;
  z-index: 7000;
  background-color: rgba(0, 0, 0, .7);
  position: fixed;
  top: 0;
  left: 0;
}

.visible {
  opacity: 1;
  display: block;
}

.leaflet-control-window {
  z-index: 2000;
  border-radius: 2px;
  margin: 8px;
  position: absolute;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, .75);
}

.control-window {
  color: #353535;
  background-color: #fff;
  font: 14px / 1.5 Helvetica Neue, Arial, Helvetica, sans-serif;
}

.leaflet-control-window .titlebar {
  min-height: 38px;
  cursor: grab;
  cursor: -webkit-grab;
  cursor: -moz-grab;
  padding: 10px 45px 10px 10px;
}

.leaflet-control-window .close {
  width: 28px;
  height: 28px;
  cursor: pointer;
  z-index: 30;
  color: #e4e4e4;
  text-align: center;
  background-color: rgba(0, 0, 0, .4);
  border-radius: 100%;
  font: 22pt / .9em Tahoma, Verdana, sans-serif;
  transition-property: background;
  transition-duration: .2s;
  transition-timing-function: linear;
  position: absolute;
  top: 8px;
  right: 8px;
}

.leaflet-control-window .close:hover {
  background-color: rgba(0, 0, 0, .65);
}

.leaflet-control-window .content {
  z-index: 29;
  margin-top: -10px;
  padding: 8px;
  overflow: auto;
}

.leaflet-control-window .promptButtons {
  text-align: right;
  padding: 16px;
}

.leaflet-control-window button {
  color: inherit;
  opacity: .5;
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 4px;
  margin: 12px 8px 0;
  padding: 8px;
  font-size: medium;
  font-weight: bold;
  text-decoration: none;
  transition-property: opacity;
  transition-duration: .2s;
  transition-timing-function: linear;
  display: inline-block;
  position: relative;
}

.leaflet-control-window button:focus {
  outline: none;
}

.leaflet-control-window button:hover {
  opacity: 1;
}

.disabled {
  opacity: .5;
  pointer-events: none;
}

.leaflet-areaselect-shade {
  background: rgba(0, 0, 0, .4);
  position: absolute;
}

.leaflet-areaselect-handle {
  width: 14px;
  height: 14px;
  cursor: move;
  background: #fff;
  border: 1px solid #666;
  position: absolute;
  box-shadow: 1px 1px rgba(0, 0, 0, .2);
}

.leaflet-bar button, .leaflet-bar button:hover {
  width: 26px;
  height: 26px;
  text-align: center;
  color: #000;
  background-color: #fff;
  border: none;
  border-bottom: 1px solid #ccc;
  line-height: 26px;
  text-decoration: none;
  display: block;
}

.leaflet-bar button {
  background-position: 50%;
  background-repeat: no-repeat;
  display: block;
  overflow: hidden;
}

.leaflet-bar button:hover {
  background-color: #f4f4f4;
}

.leaflet-bar button:first-of-type {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}

.leaflet-bar button:last-of-type {
  border-bottom: none;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.leaflet-bar.disabled, .leaflet-bar button.disabled {
  cursor: default;
  pointer-events: none;
  opacity: .4;
}

.easy-button-button .button-state {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
